th {
    border-bottom: 2px solid #444444;
    padding: 10px;
    background-color: #f6f7f8;
  }

table {
    width: 100%;
    border-top: 1px solid #444444;
    border-collapse: collapse;

  }

td {
    border-bottom: 1px solid #444444;
    padding: 10px;
  }


.WriteTitle {
    width: 100%;
    font-size: large;
    margin-bottom: 5px;
}

.WriteContent {
    width: 100%;
    height: 50vh;
    font-size: large;
    margin-bottom: 5px;
    resize: none;


}
