
.red {
    color: red;
}

.blue {
    color: blue;
}

.gray {
    color: gray;
}

/* 모달 */
.bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    
    display: flex;
    justify-content: center;
    align-items: center;
  }

.modalBox {
    background-color: #fff;
    width: 600px;
    height: auto;
    padding: 15px;
    text-align: right;
  }

.modalBox button {
    margin-bottom: 15px;
  }

.inputZipCode {
    width: 80px;
  }

.inputAddress {
    width: 300px;
  }


  /* 버튼 */

  .open-btn {
    margin: 5px;
    outline: none;
  }
  
  .custom-btn {
    width: 130px;
    height: 40px;
    padding: 10px 25px;
    border: 2px solid #000;
    font-weight: 500;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
  }
  
  /* 1 */
  .btn-1 {
    transition: all 0.3s ease;
  }
  .btn-1:hover {
     box-shadow:
     -7px -7px 20px 0px #fff9,
     -4px -4px 5px 0px #fff9,
     7px 7px 20px 0px #0002,
     4px 4px 5px 0px #0001;
  }

  
